import React from 'react';
import { BsPaperclip } from 'react-icons/bs';
import { MdComment } from 'react-icons/md';
import { MdAdd } from 'react-icons/md';
import Button from '../../utils/Button';

const CardItem = () => {
    return (
        <li className="card">
            <div className="card__image"></div>
            <span className="card__title">Github jobs challenge</span>
            <ul className="card__category">
                <li className="card__category__item">Design</li>
                <li className="card__category__item">Concept</li>
            </ul>
            <div className="card__bottom">
                <ul className="card__bottom__members">
                    <li className="card__bottom__members__item"></li>
                    <li>
                        <Button className="card__bottom__members__btn-addmember">
                            <MdAdd className="card__bottom__members__btn-addmember__icon" />
                        </Button>
                    </li>
                </ul>

                <div className="card__bottom__utils">
                    <button className="card__bottom__utils__btn">
                        <MdComment />
                        <span className="card__bottom__utils__btn__number">1</span>
                    </button>
                    <button className="card__bottom__utils__btn">
                        <BsPaperclip />
                        <span className="card__bottom__utils__btn__number">1</span>
                    </button>
                </div>
            </div>
        </li>
    );
};

export default CardItem;
