import React, { useEffect, useRef, useState } from 'react';
import socket from '../../utils/socket';
import {
    insertSpecialCharacter,
    removeHtmlClass,
    removeHtmlStyle,
    warningToast,
} from '../../utils/utils';
import Button from '../utils/Button';
import { MdInsertEmoticon } from 'react-icons/md';
import EmojiPicker from './EmojiPicker';

const MAX_CHARS = 800;

const EditDescription = ({ setIsOpen, currentDescription, boardID }) => {
    const [newDescription, setNewDescription] = useState('');
    const [isOpenEmojiPicker, setIsOpenEmojiPicker] = useState(false);
    const editorRef = useRef();
    const descriptionRef = useRef();
    const outputRef = useRef();

    const parseValue = (value) => {
        return value.replaceAll(
            /\*[a-z0-1A-Z]+/g,
            '<span class="description-word-important">$&</span>'
        );
    };
    const handleSaveDescription = async () => {
        if (currentDescription === outputRef.current.innerHTML) return setIsOpen(false);
        if (newDescription.length > MAX_CHARS)
            return warningToast(`Description is too long (max chars : ${MAX_CHARS})`);

        const description = removeHtmlStyle(outputRef.current.innerHTML);
        socket.emit('change description', { description, boardID });
        setIsOpen(false);
    };

    useEffect(() => {
        outputRef.current.innerHTML = currentDescription;
        descriptionRef.current.innerHTML = removeHtmlClass(outputRef.current.innerHTML);
        setNewDescription(outputRef.current.textContent);
    }, [descriptionRef, currentDescription]);

    const handleFormatDescription = (e) => {
        if (e.keyCode === 9) {
            e.preventDefault();
            insertSpecialCharacter(descriptionRef, 0x0009);
            outputRef.current.innerHTML = parseValue(descriptionRef.current.innerHTML);
        }
    };

    const handleSetDescription = (e) => {
        setNewDescription(e.target.textContent);
        outputRef.current.innerHTML = removeHtmlStyle(parseValue(e.target.innerHTML));
    };

    // const handlePaste = (e) => {
    //     console.log(e.clipboardData.getData('text/plain'));
    //     // descriptionRef.innerHTML = removeHtmlStyle()
    // };

    return (
        <>
            <div className="editdescription__wrapper" ref={editorRef}>
                <div
                    // onPaste={handlePaste}
                    onKeyDown={handleFormatDescription}
                    spellCheck={false}
                    onInput={handleSetDescription}
                    ref={descriptionRef}
                    className="editdescription__wrapper__input"
                    contentEditable={true}
                />
                <div ref={outputRef} className="editdescription__wrapper__output"></div>
                <button
                    className="editdescription__wrapper__btn-emoji"
                    onClick={() => setIsOpenEmojiPicker(true)}>
                    <MdInsertEmoticon />
                </button>
                <EmojiPicker
                    allowsRef={editorRef}
                    isOpen={isOpenEmojiPicker}
                    setIsOpen={setIsOpenEmojiPicker}
                />
            </div>
            <div className="editdescription__chars-wrapper">
                <span className="editdescription__chars-wrapper__item">
                    max chars : {MAX_CHARS}
                </span>
                <span
                    className={`editdescription__chars-wrapper__item ${
                        newDescription.length > MAX_CHARS ? 'maxchars-error' : ''
                    }`}>
                    current chars : {newDescription.length}
                </span>
            </div>

            <div className="editdescription__btn-wrapper">
                <Button
                    className="editdescription__btn-wrapper__btn-send"
                    onClick={() => handleSaveDescription()}>
                    Save
                </Button>
                <button
                    className="editdescription__btn-wrapper__btn-cancel"
                    onClick={() => setIsOpen(false)}>
                    Cancel
                </button>
            </div>
        </>
    );
};

export default EditDescription;
