import React, { useMemo, useRef } from 'react';
import { useSelector } from 'react-redux';
import { isEmpty } from '../../../utils/utils';
import TableItem from './ListItem';
import AddListOrCard from './AddList';

const ListManager = (props) => {
    const boardRef = useRef();
    const lists = useSelector((state) => state.boardReducer.currentBoard.lists);

    const listsMemo = useMemo(
        () =>
            !isEmpty(lists) &&
            lists.map((list) => {
                console.log('re render list');
                return <TableItem key={list._id} {...list} />;
            }),
        [lists]
    );

    const handleAddListScrollReset = () => {
        if (boardRef.current) {
            boardRef.current.scrollLeft = boardRef.current.scrollWidth;
        }
    };
    return (
        // <div className="board-wrapper">
        <div id="board" ref={boardRef}>
            {listsMemo}

            <AddListOrCard
                style={{ paddingRight: '25px' }}
                scrollLeftReset={handleAddListScrollReset}
                isFirstList={isEmpty(lists) && true}
                isAddList={true}
            />
        </div>
        // </div>
    );
};

export default ListManager;
